import {
	api,
	requestsuccess,
	requesterror
} from '../../api/request.js'
export default {
	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			goodsList: [],
			Selected: [],
			column: [],
			like: [],
		
		};
	},

	onLoad() {
			this.loadData();
			console.log(this.like)
	},
	methods: {
		
		async loadData() {

			let that = this
			/**
			 * 轮播
			 * */
		await 	uni.request({
				url: api + '/api/IndexHome/GetAdvertPosition?advertPositionCode=110',
				 success: (res) => {
					if (res.data.statusCode == requestsuccess) {
						that.carouselList = res.data.data.advertDtos
					}
				},
			})
			/**
			 * 精选
			 * **/
			 
			await uni.request({
				url: api + '/api/IndexHome/GetAdvertPosition?advertPositionCode=1222',
				 success: (res) => {
					if (res.data.statusCode == requestsuccess) {
						that.column = res.data.data
					}
				}
			})
			/**
			 * 分类
			 * **/
		await 	uni.request({
				url: api + '/api/IndexHome/GetAdvertPosition?advertPositionCode=120',
				 success: (res) => {
					if (res.data.statusCode == requestsuccess) {
						that.Selected = res.data.data
						that.Selected.title =  res.data.data.advertPositionDto.advertPositionName
					}
				}
			})
			/**
			 * 猜你喜欢
			 * **/
			await uni.request({
				url: api + '/api/IndexHome/GetAdvertPosition?advertPositionCode=001',
				 success: (res) => {
					if (res.data.statusCode == requestsuccess) {
						that.like = res.data.data
						that.like.title =  res.data.data.advertPositionDto.advertPositionName
					}
				}
			})
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].pColor;
		},
		// 详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.title;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
	},

























}
